body {
  margin: 0;
  font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
  font-size: 0.875rem; /* Equivalent to 14px */
  font-weight: 400; /* Normal weight for readability */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
  color: #1a1a1a;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.app-container {
  display: flex;
  height: 100vh;
  font-family: 'Inter', 'Arial', 'Helvetica', sans-serif; /* Updated to match ChatGPT's style */
  background-color: #ffffff;
  color: #1a1a1a;
}


.sidebar {
  width: 220px;
  padding: 1.5rem;
  background-color: #f9f9f9;
  border-right: 0px solid #e0e0e0;
  display: flex;
  flex-direction: column;
}

.sidebar-title {
  font-size: 1.25rem;
  margin-bottom: 2rem;
  font-weight: 600;
  color: #333;
}

.sidebar-menu {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.sidebar-menu li {
  /*margin-bottom: 1rem;*/
}

.sidebar-menu a {
  text-decoration: none;
  color: #333;
  padding: 0.3rem 1rem;
  display: block;
  border-radius: 8px;
  transition: background 0.2s ease, color 0.2s ease;
}

.sidebar-menu a:hover {
  background-color: #e5e7eb;
  color: #000;
}

/* Highlight the active menu item */
.sidebar-menu a.active {
  background-color: #00bbc2; /* Highlight color */
  color: #fff;
  font-weight: 600;
}

.main-content {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
}

